$color-hover: #00acdc;

.card {
  padding: 32px 16px 16px;
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  &__image {
    display: block;
    margin: 0 auto;
    width: 160px;
    height: 134px;
  }
  &__title {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: #060b35;
  }
  &__code {
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
  }
  &__section {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.card_title-position {
  margin-top: 40px;
}

.card_code-position {
  margin-top: 4px;
}

.card_section-rating {
  margin-top: 16px;
}

.card_review {
  font-style: normal;
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
}

.card_section-price {
  margin-top: 24px;
}

.card_price-title {
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.card_price-value {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}

.card__button {
  margin-top: 16px;
  width: 166px;
  height: 40px;
}

.card_button-link {
  border: 1px solid $color-hover;
  background: $color-hover;
  border-radius: 5px;
  text-transform: uppercase;
  text-decoration: none;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card_button-link:hover {
  color: $color-hover;
  background: #fff;
}
